import { Component } from '@angular/core';
import { IonicPage, ModalController, NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the CreditShieldApplyPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-credit-shield-apply',
  templateUrl: 'credit-shield-apply.html',
})
export class CreditShieldApplyPage {

  listCard = [
    {
      image: 'assets/imgs/cc_barca.jpg',
      cardName: 'Mega Travel Card',
      cardNo: '4312-2678-9012-3456',
      model: false
    },
    {
      image: 'assets/imgs/cc_barca.jpg',
      cardName: 'Mega Carrefour Card',
      cardNo: '4312-2678-9012-3456',
      model: false
    },
    {
      image: 'assets/imgs/cc_barca.jpg',
      cardName: 'Mega Platinum Card',
      cardNo: '4312-2678-9012-3456',
      model: false
    },
  ];
  snk: boolean = false;
  isChecked: boolean = false;

  constructor(public navCtrl: NavController, public navParams: NavParams, public modal: ModalController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CreditShieldApplyPage');
  }

  goBack() {
    this.navCtrl.pop();
  }

  selectCard(item) {
    item.checked = !item.checked;
  }

  openSnk() {
    this.navCtrl.push("CreditShieldSnkPage");
  }

  handlerRiplay(value: boolean) {
    if (value == false) {
      this.isChecked = !this.isChecked
    } else {
      const modalManfaat = this.modal.create("PdfViewerPage");
      modalManfaat.present();

      modalManfaat.onDidDismiss(() => {
        this.isChecked = true;
      });
    }
  }

  next() {
    if (!this.snk || !this.isChecked) {
      alert("Pastikan Anda telah mengisi seluruh data dan menyetujui Syarat & Ketentuan serta Ringkasan Informasi Produk & Layanan untuk melanjutkan");
      return false;
    } else {
      console.log('listCard', this.listCard);

      let selectedCard = [];
      this.listCard.forEach(el => {
        if (el.model == true) {
          selectedCard.push({
            nama: el.cardName,
            card: el.cardNo
          });
        }
      })
      console.log('selectedListCard', selectedCard);
    }
  }

}



